/*============================================================================
	#Global
==============================================================================*/
html {
	box-sizing: border-box;
	height: 100%;
	scroll-behavior: smooth;
}

*,
*:before,
*:after {
	box-sizing: inherit;
	outline: none;

	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
	// background: #f1f1f1;
	height: 100%;
}

img {
	height: auto;
	max-width: 100%;
	vertical-align: top;
}

svg {
	vertical-align: top;
}


