/*============================================================================
	COMMON
==============================================================================*/
.container {
	@include wrapper();
}

.row {
	@include row-flex();
}

@media (max-width: 1440px) {

	.container {
		max-width: 1200px;
		padding: 0 20px;
	}

}

@media (max-width: 1200px) {

	.container {
		max-width: 660px;
		padding: 0 20px;
	}

}
.section-test {

	.row {
		@include row-flex();
	}

	.col {
		@include col();
		@include size(12);
	}
}

.body {

	&::before {
		content: '';
		position: fixed;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		background: #000;
		opacity: 0;
		visibility: hidden;
		pointer-events: none;
		z-index: 100;
		transition: all 0.3s ease;
	}


	&.overlay {
		position: relative;

		&::before {
			opacity: 0.6;
			visibility: visible;
			pointer-events: all;
		}

		

		.header .submenu{
			z-index: 101;
		}
	}
}

.sticky-nav {
	position: fixed;
	top: 50%;
	right: 50px;
	z-index: 10;
	transform: translateX(100%) translateY(-50%);


	&__item {
		background: $colorMain;
		margin-bottom: 1px;
	}

	.button-primary {
		padding-right: 20px;
		transition: transform 0.3s ease;

		.icon {
			margin-right: 16px;
		}

		&:hover {
			transform: translateX(calc(-100% + 50px));
			background: inherit;
		} 
	}
}

.content {
	.container {
		display: flex;
		flex-wrap: wrap;
	}

	&__aside {
		flex: 0 0 300px;
		max-width: 300px;
		margin-right: 60px;

		@media (max-width: 1200px) {
			flex: 0 0 100%;
			max-width: 100%;
			margin-right: 0;
		}
	}

	&__main {
		flex: 1;

		@media (max-width: 1440px){
			max-width: 800px;
			flex: 0 0 800px;
		}

		@media (max-width: 1200px) {
			flex: 0 0 100%;
			max-width: 100%;
		}
	}
}